/* eslint-disable array-callback-return */
import { useEffect, useState, useContext } from "react";

// Components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "../../index.css";
// Dependencies
import { ListCategorias } from "../../services/category";
import SearchComponent from "./search";
import Context from "../context/UserContext";
import { ErrorBoundary } from "./errorsBoundary";
// import VideosListUser2 from './videoListUser2'
 import VideosListUser from './videoListUser'

/**
 * Función para mostrar los videos con tipo Pelicula en la pestaña Peliculas
 * @param {object} peliculas
 * @returns Componente del buscador y componente lista (Dependendiendo si el usuario es admin o no, se retorna un componente distinto)
 */
const PeliculasList = ({ peliculas, ...props }) => {
  const contenedorCarousel = document.getElementById('carousel');

  const [query, setQuery] = useState("");
  const [categories, setCategories] = useState([]);
  const [searchParam] = useState(["title_espanol", "description_esp"]);
  const [searchParam2] = useState(["categoria"]);
  const { user } = useContext(Context)
  const [filterParam, setFilterParam] = useState("All");

  /**
   * Función para traer la lista de categorias 
   */
  const listCategorias = async () => {
    try {
      const res = await ListCategorias();
      setCategories(res);
    } catch (error) {
      console.log("Error");
    }
  };

  useEffect(() => {
    listCategorias();
  }, []);

  /**
   * Función para normalizar y remover acentos de un string
   * @param {string} str 
   * @returns {string} String sin acentos
   */
  const removeAccents = (str) => {
    return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
  };

  /**
   * Función para realizar la búsqueda mediante el componente search
   * @param {*} videos 
   * @returns Parámetros de la búsqueda
   */
  const search = (videos) => {
    if (!query) {
      return videos;
    } else if (query) {
      contenedorCarousel.style.visibility = 'hidden';
      contenedorCarousel.style.height = '10px';
      const queryLowerCase = removeAccents(query.toLowerCase());
      return videos.filter((dato) => {
        const titleMatches = removeAccents(dato.title_espanol)
          .toString()
          .toLowerCase()
          .indexOf(queryLowerCase) > -1;
        const descriptionMatches = removeAccents(dato.description_esp)
          .toString()
          .toLowerCase()
          .indexOf(queryLowerCase) > -1;
        const categoryMatches = dato.categorias.some((categoria) =>
          removeAccents(categoria.categoria.toLowerCase()).includes(queryLowerCase)
        );

        return titleMatches || descriptionMatches || categoryMatches;
      });
    } else {
      return videos.filter((dato) =>
        removeAccents(dato.categorias.categoria.toString())
          .toLowerCase()
          .indexOf(removeAccents(query.toLowerCase())) > -1
      );
    }
  };

  /**
   * Función para realizar el filtro por categoría
   * @param {*} categories 
   * @returns Resultado del filtro
   */
  const search2 = (categories) => {
    return categories.filter((item) => {
      if (item.categoria === filterParam && contenedorCarousel) {
        contenedorCarousel.style.visibility = "hidden";
        contenedorCarousel.style.height = "10px";

        return searchParam2.some((parameter) => {
          return (
            removeAccents(item[parameter])
              .toString()
              .toLowerCase()
              .indexOf(removeAccents(query.toLowerCase())) > -1
          );
        });
      } else if (filterParam === "All" && contenedorCarousel) {
        contenedorCarousel.style.visibility = "visible";
        contenedorCarousel.style.height = "100%";
        return peliculas.filter((item) => {
          return searchParam.some((parameter) => {
            return (
              removeAccents(item[parameter])
                .toString()
                .toLowerCase()
                .indexOf(removeAccents(query.toLowerCase())) > -1
            );
          });
        });
      }
    });
  };

  if (user) {
    if (user.is_superuser) {
      return (
        <div className="">
          <SearchComponent
            query={query}
            setQuery={setQuery}
            filterParam={filterParam}
            setFilterParam={setFilterParam}
            categories={categories}
          ></SearchComponent>
          <ErrorBoundary>
            <VideosListUser
              videos={peliculas}
              categories={categories}
              search={search}
              search2={search2}
            />
          </ErrorBoundary>
        </div>
      );
    }
    return (
      <div>
        <SearchComponent
          query={query}
          setQuery={setQuery}
          filterParam={filterParam}
          setFilterParam={setFilterParam}
          categories={categories}
        ></SearchComponent>
        <ErrorBoundary>
          <VideosListUser
            videos={peliculas}
            categories={categories}
            search={search}
            search2={search2}
          />
        </ErrorBoundary>
      </div>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
};

export default PeliculasList;
